.file-input {
  align-items: center;
  margin: 4px;
  display: flex;
}

.file-input label {
  all: unset;
  white-space: nowrap;
  cursor: pointer;
  background-color: #eee;
  border-radius: 32px;
  padding: 8px 16px;
  font-size: 14px;
  transition: background-color 50ms;
}

.file-input label:before {
  content: "image";
  vertical-align: bottom;
  margin-right: 4px;
  font-family: Material Symbols Outlined;
  font-size: 20px;
  line-height: 20px;
}

.file-input label:hover {
  background-color: #ddd;
}

.file-input input[type="file"] {
  display: none;
}

.text-input {
  align-items: center;
  margin-left: 8px;
  display: flex;
}

.text-input label {
  all: unset;
  white-space: nowrap;
  cursor: pointer;
  margin-right: 8px;
  font-size: 14px;
  transition: background-color 50ms;
}

.text-input input[type="text"], .text-input input[type="number"] {
  all: unset;
  border-radius: 4px;
  outline: 1px solid #ccc;
  padding: 6px 8px;
  font-size: 14px;
  line-height: 14px;
  transition: background-color .1s, outline .1s;
}

.text-input input[type="text"]:hover, .text-input input[type="number"]:hover {
  outline-color: #888;
}

.text-input input[type="text"]:focus, .text-input input[type="number"]:focus {
  background-color: #59e1;
  outline: 2px solid #59e;
}

.icon {
  vertical-align: bottom;
  margin-right: 4px;
  font-family: Material Symbols Outlined;
  font-size: 20px;
  line-height: 20px;
}

