.file-input
  display: flex
  align-items: center
  margin: 4px

  label
    all: unset
    padding: 8px 16px
    background-color: #eee
    border-radius: 32px
    font-size: 14px
    white-space: nowrap
    cursor: pointer
    transition: background-color 50ms
    
    &::before
      content: "image"
      font-family: "Material Symbols Outlined"
      font-size: 20px
      line-height: 20px
      vertical-align: bottom
      margin-right: 4px

    &:hover
      background-color: #ddd
      

  input[type=file]
    display: none

.text-input
  display: flex
  align-items: center
  margin-left: 8px

  label
    all: unset
    margin-right: 8px
    font-size: 14px
    white-space: nowrap
    cursor: pointer
    transition: background-color 50ms
      

  input[type=text], input[type=number]
    all: unset
    padding: 6px 8px
    border-radius: 4px
    outline: 1px solid #ccc
    font-size: 14px
    line-height: 14px
    transition: background-color .1s, outline .1s

    &:hover
      outline-color: #888

    &:focus
      background-color: #59e1
      outline: 2px solid #59e

.icon
  font-family: "Material Symbols Outlined"
  font-size: 20px
  line-height: 20px
  vertical-align: bottom
  margin-right: 4px